export const insertLikeSong = (song) => {
  let localLikeSongs = localStorage.getItem('likeSongs')
  if (!localLikeSongs) return

  let likeSongs = JSON.parse(localLikeSongs)

  if (querySongFromLocalStrong(song.contentId)) return
  likeSongs["song-list"].push(song)
  localStorage.setItem('likeSongs', JSON.stringify(likeSongs))
}

export const querySongFromLocalStrong = (contentId) => {
  let localLikeSongs = localStorage.getItem('likeSongs')
  if (!localLikeSongs) return null

  let matchSong = JSON.parse(localLikeSongs)["song-list"].filter(song => {
    return song.contentId === contentId
  })
  if (matchSong.length) return matchSong[0]

  return null
}