<template>
  <div class="search-container">
    <div class="search-input-wrapper">
      <input type="text" placeholder="搜索音乐" v-model="searchText" @keydown="searchKeydown" />
      <a href="javascript:;" class="shadow-btn icon-btn search-btn" @click="search"></a>
    </div>
    <div class="result-area" v-bottom-load="searchMoreSong">
      <div class="song-list" v-for="song in result" :key="song.contentId" @click="play(song)">
        <div class="list-left">
          <span class="song-name">
            {{song.name}}
            <span class="singer-name" v-for="singer in song.singers" :key="singer.id">{{singer.name}}</span>
          </span>
          <span class="albums" v-if="song.albums">《{{song.albums[0].name}}》</span>
        </div>
        <div class="list-right">
          <a href="javascript:;" v-if="song.inList" class="shadow-btn icon-btn like-btn liked-btn"></a>
          <a href="javascript:;" v-else class="shadow-btn icon-btn like-btn" @click.stop="like(song)"></a>
        </div>
      </div>
    </div>
    <transition name="fade">
      <div v-show="loading" class="loading-bar shadow-btn iconfont">
        <i>&#xe65d;</i>
        <span>加载中</span>
      </div>
    </transition>
  </div>
</template>

<script>
import {
  API
} from '../api/index'
import {
  insertLikeSong,
  querySongFromLocalStrong
} from '../common/common'
export default {
  data() {
    return {
      searchText: '艾福杰尼',
      result: [],
      page: 0,
      pageSize: 20,
      loading: false
    }
  },
  created() {
    this.searchMoreSong()
  },
  methods: {
    search() {
      this.page = 0
      this.result = []
      this.searchMoreSong()
    },

    searchKeydown($event) {
      if ($event.keyCode === 13) this.search()
    },

    searchMoreSong() {
      if (this.loading) return
      this.loading = true
      this.page++
      this.$http.get(API.SEARCH_SONG_BY_KEYWORD, {
        '&ua': 'Android_migu',
        'text': this.searchText,
        'pageNo': this.page,
        'pageSize': this.pageSize,
        'version': '5.0.1',
        'searchSwitch': {
          'song': 1,
          'album': 0,
          'singer': 0,
          'tagSong': 0,
          'mvSong': 0,
          'songlist': 0,
          'bestShow': 1
        }
      }).then(res => {
        let result = res && res.songResultData && res.songResultData.result
        result.forEach(song => {
          song['inList'] = !!querySongFromLocalStrong(song.contentId)
        })
        this.result = [...this.result, ...result]
      }).catch(error => {
        console.log(error)
        this.page--
      }).finally(() => {
        this.loading = false
      })
    },

    getFilterSong(song) {
      const {
        lyricUrl,
        name,
        singers,
        albums,
        contentId,
        imgItems
      } = song

      return {
        lyricUrl,
        name,
        singers,
        albums,
        contentId,
        imgItems
      }
    },

    play(song) {
      this.$emit('play', this.getFilterSong(song))
    },

    like(song) {
      song['inList'] = true
      insertLikeSong(this.getFilterSong(song))
    }
  }
}
</script>

