<template>
  <div id="app">
    <div class="header-options" v-show="pageState === 'player'">
      <a href="javascript:;" class="shadow-btn icon-btn menu-btn"></a>
      <a href="javascript:;" class="shadow-btn icon-btn search-btn" @click="searchPage"></a>
    </div>
    <Search @play="play" v-show="pageState === 'search'"></Search>
    <Player v-show="pageState === 'player' && songInfo.audioUrl" :songInfo.sync="songInfo" ref="player"></Player>
  </div>
</template>

<script>
import Player from './components/player.vue'
import Search from './components/search.vue'
import { API } from './api/index'

export default {
  components: {
    Player,
    Search
  },
  data() {
    return {
      songInfo: {
        audioUrl: '',
        lrc: [],
        coverUrl: '',
      },
      toneFlag: 'HQ', //HQ高清 SQ无损
      pageState: 'search'
    }
  },
  methods: {
    getAudioUrl(toneFlag, contentId) {
      return `${API.GET_AUDIO_INFO}?toneFlag=${toneFlag}&netType=00&copyrightId=0&contentId=${contentId}&resourceType=${toneFlag === 'HQ' ? 2 : 'E'}&channel=0`
    },

    searchPage() {
      this.pageState = 'search'
    },

    async play(song) {
      try {
        let lrc = await this.$http.get(song.lyricUrl)
        this.lrc = lrc.split('\n').map(singlelrc => {
          return {
            time: this.timeToSencond(singlelrc.substr(1, 8)),
            value: singlelrc.substr(10),
            key: singlelrc + Date.now(),
            current: false
          }
        })
      } catch (error) {
        console.log(error)
        this.lrc = []
      }
      this.songInfo = {
        name: song.name,
        singer: song.singers.map(singer => singer.name).join('，'),
        albumsName: song.albums && song.albums[0] && song.albums[0].name || '',
        audioUrl: this.getAudioUrl(this.toneFlag, song.contentId),
        coverUrl: song.imgItems[1].img,
        lrc: this.lrc
      }
      this.$refs.player.update(this.songInfo)
      this.pageState = 'player'
    },

    timeToSencond(time) {
      if (!time) return
      let timeArr = time.split(':')
      let m = parseInt(timeArr[0])
      let s = parseFloat(timeArr[1])
      return (m * 60 + s).toFixed(2)
    }
  }
}
</script>

