<template>
  <div class='player-container'>
    <div class="song-info">
      <span class="name">{{songName}}</span>
      <span class="singer">{{singer}}</span>
      <span class="albums-name">《{{albumsName}}》</span>
    </div>
    <div class="cover-area">
      <div class="cover-wrapper">
        <img :src="coverUrl" alt="">
      </div>
    </div>
    <div class="lyric-area">
      <ul class="lyric" ref="lyric" :style="{top: lyricTop}">
        <li v-for="(signleLrc, index) in lrc" :key="signleLrc.key" :class="currentLrc(index) ? 'current-lrc' : ''">{{signleLrc.value}}</li>
      </ul>
      <div class="lyric-mask"></div>
    </div>
    <div class="player-bottom">
      <div class="progress-bar" ref="progress" @click="progressClick">
        <div class="progress-value" :style="{width:progress}"></div>
        <div class="progress-controller" :style="{left:progress}"></div>
      </div>
      <div class="time-area">
        <span class="current-time">{{currentTime}}</span>
        <span class="totle-time">{{duration}}</span>
      </div>
      <div class="options-area">
        <a href="javascript:;" class="shadow-btn icon-btn option-btn volume-btn"></a>
        <a href="javascript:;" class="shadow-btn icon-btn option-btn prev-btn"></a>
        <a href="javascript:;" class="shadow-btn icon-btn option-btn" :class="playState === 'play' ? 'pause-btn' : 'play-btn'" @click="play"></a>
        <a href="javascript:;" class="shadow-btn icon-btn option-btn next-btn"></a>
        <a href="javascript:;" class="shadow-btn icon-btn option-btn list-btn"></a>
      </div>
      <audio style="display: none;" ref="audio" :src="audioUrl" controls loop="true" @timeupdate="timeupdate" @canplay="canplay"></audio>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    songInfo: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      songName: '',
      singer: '',
      albumsName: '',
      audioUrl: '',
      coverUrl: '',
      lrc: [],
      playState: 'pause',
      progress: '0%',
      currentTime: '00:00:00',
      duration: '00:00:00',
      currentSeconds: 0,
      lyricTop: '0px'
    }
  },
  created() {
    this.init(this.songInfo)
  },
  methods: {
    init(songInfo) {
      this.songName = songInfo.name
      this.singer = songInfo.singer
      this.albumsName = songInfo.albumsName
      this.audioUrl = songInfo.audioUrl
      this.coverUrl = songInfo.coverUrl
      this.lrc = songInfo.lrc
    },

    update(songInfo) {
      this.init(songInfo)
      this.$refs.audio.load()
    },

    canplay() {
      this.$refs.audio.play()
      this.playState = 'play'
    },

    play() {
      if (this.playState === 'play') {
        this.$refs.audio.pause()
        this.playState = 'pause'
      } else {
        this.$refs.audio.play()
        this.playState = 'play'
      }
    },

    timeupdate($event) {
      const audio = $event.target
      const duration = audio.duration
      const currentTime = audio.currentTime
      this.duration = this.formatSeconds(duration || 0)
      this.currentTime = this.formatSeconds(currentTime)
      this.currentSeconds = currentTime
      this.progress = audio.currentTime / audio.duration * 100 + "%";
    },

    formatSeconds(value) {
      let result = parseInt(value)
      let h = Math.floor(result / 3600) < 10 ? '0' + Math.floor(result / 3600) : Math.floor(result / 3600)
      let m = Math.floor((result / 60 % 60)) < 10 ? '0' + Math.floor((result / 60 % 60)) : Math.floor((result / 60 % 60))
      let s = Math.floor((result % 60)) < 10 ? '0' + Math.floor((result % 60)) : Math.floor((result % 60))
      result = `${h}:${m}:${s}`
      return result
    },

    currentLrc(index) {
      let minTime = this.lrc[index] ? this.lrc[index].time : 0
      let maxTime = this.lrc[index + 1] ? this.lrc[index + 1].time : 0
      let currentTime = Math.floor(this.currentSeconds + 1)
      let isCurrentLrc = minTime <= currentTime && currentTime <= maxTime
      if (isCurrentLrc) {
        let currentLrc = document.querySelectorAll('.current-lrc')[0]
        this.lyricTop = `-${currentLrc && currentLrc.offsetTop - 30}px`
      }
      return isCurrentLrc
    },

    progressClick($event) {
      const offsetWidth = this.$refs.progress.offsetWidth
      const offsetX = $event.offsetX
      const progress = offsetX / offsetWidth
      const duration = this.$refs.audio.duration
      this.$refs.audio.currentTime = duration * progress
    }
  }
}
</script>

